<template>
    <div class="1">
        <vx-card class="mb-base">
            <div class="md:flex">
                <div class="lg:w-1/5 mt-4 md:mt-0">
                <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" :options="pilihanA" v-model="pilihanASelected" @input="updatePilihan"></v-select>
                </div>
                <div class="lg:w-2/5 mt-4 md:mt-0 md:ml-4">
                <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" label="nama" :options="pilihanB" v-model="pilihanBSelected"></v-select>
                </div>
                <div class="lg:w-1/4 mt-4 md:mt-0 md:ml-4">
                <datepicker data-vv-validate-on="blur" :placeholder="'-- Bulan --'" :minimumView="'month'" :maximumView="'year'" :format="'MMMM yyyy'" v-model="monthSelected" ></datepicker>
                </div>
                <div class="w-10 mt-4 md:mt-0 md:ml-4">
                <vs-button :disabled="!validateForm" radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline; margin-right:30px;"></vs-button>
                </div>   
            </div>
        </vx-card>
        <vx-card class="mb-base" v-show="cardShow">
            <vs-button size="small" color="grey" v-on:click="printThis" class="float-right mb-2 px-3 pt-1 pb-0"><feather-icon icon="CopyIcon" /></vs-button>
            <div class="p-2" ref="printcontent">
                <h5>{{ title }}</h5>
                <div class="w-full">
                    <vs-table :data="sales">
                        <template slot="thead" >
                            <vs-th class="bg-primary text-white" style="border: 0px solid #DDDDDD;">
                                PERIODE
                            </vs-th>
                            <vs-th sort-key="bln_ini" class="bg-primary text-white date" style="border: 0px solid #DDDDDD;">
                                {{ titleColumnTableNow }}
                            </vs-th>
                            <vs-th sort-key="bln_lalu" class="bg-primary text-white date" style="border: 0px solid #DDDDDD;"    >
                                {{ titleColumnTableLast }}
                            </vs-th>
                            <vs-th sort-key="growth" class="bg-primary text-white" width="100px" style="border: 0px solid #DDDDDD;">
                                GROWTH
                            </vs-th>
                        </template>
            
                        <template slot-scope="{data}" >
                        <vs-tr :state="data[indextr].periode == '' ? 'primary' : null " :data="tr" :key="indextr" v-for="(tr, indextr) in data">
                            <vs-td :data="data[indextr].periode" :style="data[indextr].periode == '' ? 'padding: 0px; border: 0px; border-bottom: 1px solid #DDDDDD;' : 'border: 0px; border-bottom: 1px solid #DDDDDD;'">
                                {{data[indextr].periode}}
                            </vs-td>
                            
                            <vs-td :data="data[indextr].now_omset" class="text-center"  :style="data[indextr].periode == '' ? 'padding: 0px; border: 0px; border-bottom: 1px solid #DDDDDD;' : 'border: 0px; border-bottom: 1px solid #DDDDDD;'">
                                <span>{{data[indextr].now_omset.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}</span><br>
                                <small class="text-grey">{{data[indextr].now_not}}</small>
                            </vs-td>

                            <vs-td :data="data[indextr].past_omset" class="text-center"  :style="data[indextr].periode == '' ? 'padding: 0px; border: 0px; border-bottom: 1px solid #DDDDDD;' : 'border: 0px; border-bottom: 1px solid #DDDDDD;'">
                                <span>{{data[indextr].past_omset.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}</span> <br>
                                <small class="text-grey">{{data[indextr].past_not}}</small>
                            </vs-td>
    
                            <vs-td :data="data[indextr].growth" class="text-center"  :style="data[indextr].periode == '' ? 'padding: 0px; border: 0px; border-bottom: 1px solid #DDDDDD;' : 'border: 0px; border-bottom: 1px solid #DDDDDD;'">
                                <div class="mt-3" v-if="parseInt(data[indextr].now_omset) > parseInt(data[indextr].past_omset)">
                                    <span>{{ data[indextr].growth }}</span><br>   
                                    <feather-icon size="14" class="text-success" icon="TrendingUpIcon" />
                                </div>
                                <div class="mt-3" v-else-if="parseInt(data[indextr].past_omset) > parseInt(data[indextr].now_omset)">
                                    <span>{{ data[indextr].growth }}</span><br>     
                                    <feather-icon size="14" class="text-danger" icon="TrendingDownIcon" />
                                </div>
                                <div class="mt-3" v-else-if="parseInt(data[indextr].now_omset) == parseInt(data[indextr].past_omset)">
                                    <span>{{ data[indextr].growth }}</span> 
                                </div>
                                <div v-else>
                                </div>
                            </vs-td>
                        </vs-tr>
                        </template>
                    </vs-table>
                </div>
            </div>
        </vx-card>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import html2canvas from 'html2canvas'
import { copyImageToClipboard } from 'copy-image-clipboard'
import moment from 'moment';

export default {
   data: function() {
        return {
            // Select Option 1
            pilihanA:['Region','Wilayah','Area','Cabang','Toko'],
            pilihanASelected:null,
            // Select Option 2
            pilihanB:[],
            pilihanBSelected:null,
            // Select Option 3
            monthSelected:null,
            title: '',
            titleColumnTableNow: '',
            titleColumnTableLast: '',
            sales: [],
            cardShow : false
        }
    },

    components: {
        Datepicker,
    },

    computed: {
        validateForm() {
            return !this.errors.any() && this.pilihanBSelected != null && this.monthSelected != null;
        },
    },

    methods: {
        loadDataServer() {
            this.cardShow = false
            this.title = 'TREND SALES ' + this.pilihanBSelected.nama + ' - ' + moment(this.monthSelected).locale('id').format("MMMM YYYY").toUpperCase()
            this.titleColumnTableNow = moment(this.monthSelected).locale('id').format("MMMM").toUpperCase()
            this.titleColumnTableLast = moment(this.monthSelected).subtract(1,'months').locale('id').format("MMMM").toUpperCase()
            
            this.$vs.loading()
            var postData = {
                kode : this.pilihanBSelected.kode,
                bulan :  btoa(moment(this.monthSelected).format("YYYY-MM")),
            }

            this.$http.post('/pos/sales/getSalesCompareLastMonth', postData )
            .then(response => {
                this.$vs.loading.close()
                if(response.data.status == "SUKSES"){
                    this.cardShow = true
                    this.sales = response.data.message
                } 
            })
            .catch(error => {
                // console.log(error)
                error
                this.$vs.loading.close()
            })
            // setTimeout( ()=> {
            //     this.$vs.loading.close()
            // }, 1000);
        },

        updatePilihan(value) {
            this.$vs.loading()
            this.$http.get('toko/'+ value)
            .then(response => {
                if(response.data.status == "OK"){
                    this.pilihanB = response.data.message
                }
                })
            .catch(error => {error})
            setTimeout( ()=> {
                this.$vs.loading.close()
            }, 1000);
        },

        async printThis() {
            const el = this.$refs.printcontent;

            const options = {
                type: "dataURL"
            };
            const printCanvas = await html2canvas(el, options);
            copyImageToClipboard(printCanvas.toDataURL("image/png"))
            .then(() => {
                this.$swal.fire({
                    title: 'Success',
                    text: "Gambar berhasil di copy..",
                    icon: 'success',
                    timer: 1500
                })
                // console.log('Image Copied')
            })
            .catch((error) => {
                error
                // console.log('Error: ', e.message)
            })
            // download
            // console.log(printCanvas);
            // const link = document.createElement("a");
            // link.setAttribute("download", "download.png");
            // link.setAttribute(
            //     "href",
            //     printCanvas
            //     .toDataURL("image/png")
            //     .replace("image/png", "image/octet-stream")
            // );
        }
    }
}
</script>

<style scoped>
.date {
    width: 115px;
}

svg {
    width: 14px;
    padding: 0px;
    margin: 0px;
}
</style>